import React, { useContext, useEffect, useState } from "react";
import { Card, Dropdown, Form } from "react-bootstrap";
import classes from "./Heading.module.css";
import { Button, InputGroup, FormControl } from "react-bootstrap";
import FetchProducts from "../store/FetchProducts";
import Search from "../Search/Serach";
import AuthContext from "../store/auth-context";
import firebase from "firebase/app";
import NoProducts from "../Search/NoProducts";
import SomeGames2 from "../bestGames/SomeGames";
import { Link, NavLink } from "react-router-dom";
import { GiShoppingCart } from "react-icons/gi";
import { BiSearchAlt2 } from "react-icons/bi";
import SomeGames from "../SomeGames/SomeGames";
import { database } from "../store/FireBase";

const Heading = () => {
  const [price, setprice] = useState("");
  const [games, setgames] = useState([]);
  const authCtx = useContext(AuthContext);
  const Fetch = FetchProducts();
  const products = database.ref(`/profile/productsOrder/${authCtx.token}`);
  useEffect(() => {
    let gamesList = [];
    let Total = [];
    let FakeTotal = [0, 0, 0, 0, 0];
    products.once("value", (value) => {
      const object = value.val();
      for (const key in object) {
        gamesList.push(object[key]);
      }
      Total.push(gamesList.map((games) => games.price));
      for (let i = 0; i < Total.length; i++) {
        if (Total[i].length === 0) {
          return setprice(FakeTotal.reduce((a, b) => a + b));
        }
        setprice((Total[i] || FakeTotal).reduce((a, b) => a + b));
        setgames(gamesList.map((games) => games.name));
      }
    });
  }, []);

  const SeachValue = (props) => {
    props.preventDefault();
    Fetch.setIsProducts(props.target.value);
  };

  const onKeyDown = (ev) => {
    if (ev.keyCode === 13) {
      Fetch.fetchProducts();
    }
  };

  const ProductsList = Fetch.products.map((game) => {
    return (
      <Search
        id={game.id}
        name={game.name}
        price={game.price}
        Discription={game.Dis}
        thumbnail={game.thumbnail}
      />
    );
  });

  return (
    <div>
      <div className={classes.heading}>
        <div className={classes.full}>
          <div className={classes.branding}>
            <Link className={classes.title} to="http://buyitcheap.epizy.com/">
              GamesDigital
            </Link>
            <p>Digital Games at Cheap prices</p>
          </div>
          <div className={classes.Form}>
            <InputGroup>
              <InputGroup.Text
                style={{ backgroundColor: "white", borderRadius: "white" }}
              >
                {" "}
                <BiSearchAlt2 />
              </InputGroup.Text>

              <FormControl
                id="inlineFormInputGroup"
                placeholder="Search Producs..."
                type="search"
                value={Fetch.isProducts}
                onChange={SeachValue}
                onKeyDown={onKeyDown}
              />
            </InputGroup>
          </div>
        </div>
        <div className={classes.nav}>
          <div className={classes.full}>
            <ul className={classes.navmenu}>
              <li>
                <NavLink to="/">Home</NavLink>
              </li>

              <li>
                <NavLink to="/Cart/">Cart</NavLink>
              </li>
              <li>
                <NavLink to="/Checkout">Checkout</NavLink>
              </li>
              <li>
                <NavLink to="/Login/">My account</NavLink>
              </li>
              <div className={classes.cart}></div>

              <li className={classes.Form2}>
                <Dropdown>
                  <Dropdown.Toggle variant="black" id="dropdown-basic">
                    <span style={{ color: "lightgray" }}>
                      <span style={{ color: "white" }}></span>
                      {price}
                    </span>
                    <span className={classes.count}> {games.length} items</span>
                    <span>
                      <GiShoppingCart className={classes.count2} />
                    </span>
                  </Dropdown.Toggle>
                  <Dropdown.Menu>
                    <Dropdown.Item href="#/action-1">Action</Dropdown.Item>
                    <Dropdown.Item href="#/action-2">
                      Another action
                    </Dropdown.Item>
                    <Dropdown.Item href="#/action-3">
                      Something else
                    </Dropdown.Item>
                  </Dropdown.Menu>
                </Dropdown>
              </li>
            </ul>
          </div>
        </div>
      </div>
      {!Fetch.touched && <SomeGames2 />}
      {!Fetch.touched && <SomeGames />}
      {ProductsList}
      {Fetch.touched && ProductsList.length === 0 && <NoProducts />}
    </div>
  );
};

export default Heading;
