import React, { Fragment, useEffect, useState } from "react";
import { database } from "../store/FireBase";
import GamesCard from "./GamesCard";
import classes from "./SomeGames.module.css";
import Paginations from "../Post/Pagination";
import { Col, Container, Form } from "react-bootstrap";

const SomeGames2 = () => {
  const [games, setgames] = useState([]);
  const [increasegames, setIncreasegames] = useState([]);
  const [decrease, setDecrease] = useState([]);
  const products = database.ref(`/Game`);
  const [currentPage, setCurrentPage] = useState(1);

  useEffect(() => {
    products.orderByChild("Game").once("value", function (value) {
      setgames(value.val());
    });
    products
      .orderByChild("price")
      .limitToFirst(10)
      .once("value", function (value) {
        setIncreasegames(value.val());
      });
    products
      .orderByChild("price")
      .limitToLast(10)
      .once("value", function (value) {
        setDecrease(value.val());
      });
  }, []);
  const resultPerPage = 12;

  const indexOfFirstPost = (currentPage - 1) * resultPerPage;
  const indexOfLastPost = currentPage * resultPerPage;
  const currentPosts = games.slice(indexOfFirstPost, indexOfLastPost);
  const nextPage = (number) => setCurrentPage(currentPage + number);

  const Increasegames = () =>
    increasegames.slice(indexOfFirstPost, indexOfLastPost);
  const Dcreasegames = () => decrease.slice(indexOfFirstPost, indexOfLastPost);

  const GamesList = currentPosts.map((game) => (
    <GamesCard
      key={game.id}
      id={game.id}
      name={game.name}
      price={game.price}
      Discription={game.Dis}
      thumbnail={game.thumbnail}
    />
  ));

  return (
    <Fragment>
      <ul className={classes.some}>
        <li>
          <h3 style={{ textAlign: "left", color: "gray" }}>Shop</h3>
        </li>
        <li>
          <Paginations
            className={classes.Paginations}
            products={games}
            resultPerPage={resultPerPage}
            currentPage={currentPage}
            nextPage={nextPage}
          />
        </li>
      </ul>
      <Container>
        <Form.Select
          style={{ width: "15%" }}
          aria-label="Floating label select example"
        >
          <option value={increasegames} onChange={Increasegames}>
            One
          </option>
          <option value={decrease} onChange={Dcreasegames}>
            Two
          </option>
          <option value="3">Three</option>
        </Form.Select>
      </Container>
      <div style={{ marginTop: "10px" }}>
        <Col md={{ offset: 1 }}>{GamesList}</Col>
      </div>
      <Container>
        <Form.Select
          style={{ width: "15%" }}
          aria-label="Floating label select example"
        >
          <option value="1">One</option>
          <option value="2">Two</option>
          <option value="3">Three</option>
        </Form.Select>
      </Container>
    </Fragment>
  );
};

export default SomeGames2;
