import React, { useContext, useEffect, useState } from "react";
import { Offcanvas, Button } from "react-bootstrap";
import classes from "./Heading.module.css";
import AuthContext from "../store/auth-context";
import { database } from "../store/FireBase";
import { Link } from "react-router-dom";

const Profile = (props) => {
  const [state, setstate] = useState("");
  const [date, setDate] = useState("");
  const [totalItem, setTotalItem] = useState(false);
  const authCtx = useContext(AuthContext);

  const products = database.ref(`/profile/productsOrder/${authCtx.token}`);

  useEffect(() => {
    products.once("value", (value) => {
      let gamesList = [];
      const object = value.val();
      for (const key in object) {
        gamesList.push(object[key]);
      }
      setTotalItem(gamesList.length);
    });
    database.ref(`/profile/${authCtx.token}`).on("value", (value) => {
      if (authCtx.token) {
        setstate(value.val().name.split(" "));
        var timestamp = value.val().createdAt;
        var date2 = new Date(timestamp);
        setDate(date2);
      } else {
        return false;
      }
    });
  }, [authCtx.token]);

  return (
    <div className={classes.profile}>
      <h1 style={{ fontFamily: "Helvetica", textAlign: "center" }}> {state}</h1>
      <h4>Member Since {"NaNaaa"}</h4>
      <Link to="/Cart">{totalItem} Item In Cart</Link>
      <Button onClick={props.LogOut}>Logout</Button>
    </div>
  );
};
export default Profile;
