import React, { useEffect, useState } from 'react'
import { database} from '../store/FireBase';
import { Button, Card} from 'react-bootstrap'
// import classes from './SomeGames.module.css'
import classes from './games.module.css'
import { Link } from 'react-router-dom';

 const SomeGames = () => {
 const [games, setgames] = useState([])


    const products = database.ref(`/Game`)
    useEffect(() => {
        products.orderByChild("Game").on('value',function(value) { 
            setgames(value.val())
        })
    }, [])

     const gamesList = games.slice(15,16).map((game)=>
     <div style={{ width: '30rem', marginLeft: 'auto',marginRight: 'auto' }}  key={game.id} >
     <Card.Img variant="top" src={game.thumbnail} />
     <Card.Body>
       {/* <Card.Title>{game.name}</Card.Title> */}
       <Button  style={{float:'left'}}><Link to={`/Info/${game.id}`}>Buy {game.name}</Link></Button>
     </Card.Body>
   </div>
     )
     const gamesList2 = games.slice(12,13).map((game)=>
     <div style={{ width: '30rem' , marginLeft: 'auto',marginRight: 'auto' }}  key={game.id}>
     <Card.Img variant="top" src={game.thumbnail} />
     <Card.Body>
       {/* <Card.Title></Card.Title> */}
       <Button  style={{float:'right'}}><Link to={`/Info/${game.id}`}>Buy {game.name}</Link></Button>
     </Card.Body>
   </div>
     )




    return (
        <div >
         <div className={classes.text}>
         <h2  style={{fontWeight:'unset' ,fontSize:'3rem',color:'darkgray'}}>GAMES</h2>
         <h2 >Best Selling Games</h2>
         <div className={classes.product}>
          {gamesList} 
          {gamesList2} 
          </div>
          <h6 style={{fontWeight:'-moz-initial',fontSize:'12px',color:'darkgray'}}>Top Most Selling Games</h6>
          </div>
        </div>
    )
}

export default SomeGames
