import React, { useContext, useEffect, useState } from "react";
import { Image, Button, Container, Figure, Col } from "react-bootstrap";
import classes from "./games.module.css";
import { Link, useParams } from "react-router-dom";
import { database } from "../store/FireBase";
import AuthContext from "../store/auth-context";
import { BsFillEmojiSmileFill } from "react-icons/bs";
import { AiTwotoneHome } from "react-icons/ai";
import { BiRupee } from "react-icons/bi";

const Games = (props) => {
  const [True, setTrue] = useState(false);
  const [game, setGame] = useState("");
  const authCtx = useContext(AuthContext);
  const { id } = useParams();

  const onAddToCart = () => {
    const products = database.ref(
      `/profile/productsOrder/${authCtx.token}/${game.name}`
    );
    products.set(game);
  };
  const Game = database.ref(`/Game`);
  const products = database.ref(`/profile/productsOrder/${authCtx.token}`);

  useEffect(() => {
    Game.orderByChild("id")
      .equalTo(id)
      .once("child_added", function (value) {
        setGame(value.val());
      });
  }, []);
  if (game) {
    products
      .orderByChild("name")
      .equalTo(game.name)
      .once("child_added", function (value) {
        setTrue(value.val().name === game.name);
      });
  } else {
    console.log(game);
  }

  return (
    <div>
      <Container>
        <div className={classes.somegames1}>
          <Link to="/">
            <AiTwotoneHome style={{ marginBottom: "4px" }} /> Home
          </Link>
          <h5>{game.name}</h5>
        </div>
      </Container>
      <div className={classes.somegames}>
        <Container>
          <Figure.Image alt="171x180" src={game.thumbnail} />
        </Container>
        <Container>
          <Col md={{ offset: 2 }}>
            <h2>{game.name}</h2>
            <h3 style={{ marginTop: "80px" }}>
              <BiRupee style={{ marginBottom: "4px" }} />
              {game.price}
            </h3>
            <h5 style={{ marginTop: "50px" }}>{game.Dis}</h5>
            <h5 style={{ marginTop: "50px", color: "green" }}>
              <BsFillEmojiSmileFill
                style={{ marginBottom: "4px", marginRight: "5px" }}
              />
              {game.stock} in stock
            </h5>
            {!True && (
              <Button onClick={onAddToCart} style={{ marginTop: "60px" }}>
                {" "}
                Add To Cart
              </Button>
            )}
            {True && (
              <Link to="/cart">
                <Button>Go To Cart</Button>
              </Link>
            )}
            <h6 style={{ marginTop: "80px" }}>
              Category:
              <Link to="https://www.youtube.com/channel/UCRyrRcrQfnQy2t8S-3aOdew">
                {game.categories}
              </Link>
            </h6>
            <h6>
              Tags: <Link>{game.tags}</Link>
            </h6>
          </Col>
        </Container>
      </div>
    </div>
  );
};

export default Games;
