import React, { Fragment } from 'react'
import { Pagination } from 'react-bootstrap'
import classes from './post.module.css'

const Paginations = (props) => {

  const numPages = Math.ceil(props.products.length/props.resultPerPage )

if (props.currentPage === 1 && numPages > 1) {
return (
<div className={classes.post}>
<Pagination>
<Pagination.Prev onClick={() => props.nextPage(1)}/>
<Pagination.Item onClick={() => props.nextPage(1)}>{props.currentPage+1}</Pagination.Item>
<Pagination.Next onClick={() => props.nextPage(1)}/>
</Pagination>
</div>
)
}
  if (props.currentPage === numPages && numPages>1) {
  return (
  <div className={classes.post}>
        <Pagination>
        <Pagination.Prev onClick={() => props.nextPage(-1)}/>
        <Pagination.Item onClick={() => props.nextPage(-1)}>{props.currentPage-1}</Pagination.Item>
        <Pagination.Next onClick={() => props.nextPage(-1)}/>
        </Pagination>
  </div>
  )
        }
        if (props.currentPage < numPages ) {
        return (
        <div className={classes.post}>
        <Pagination>
        <Pagination.Prev onClick={() => props.nextPage(-1)}/>
        <Pagination.Item onClick={() => props.nextPage(-1)}>{props.currentPage-1}</Pagination.Item>
        <Pagination.Item onClick={() => props.nextPage(1)}>{props.currentPage+1}</Pagination.Item>
        <Pagination.Next onClick={() => props.nextPage(1)}/>
        </Pagination>
        </div>
        )
        }



  return (
    <div className={classes.post}>
{}
    </div>
  )
}

export default Paginations
