import React, { useContext, useEffect, useRef, useState } from "react";
import { Form, Button } from "react-bootstrap";
import AuthContext from "../store/auth-context";
import { auth, database } from "../store/FireBase";
import firebase from "firebase/app";
import classes from "./Login.module.css";
import { BsFacebook, BsGoogle } from "react-icons/bs";
import Profile from "../Heading/Profile";
import { Link } from "react-router-dom";

const Login = () => {
  const emailInputRef = useRef();
  const [state, setstate] = useState("");
  const passwordInputRef = useRef();
  const nameInputRef = useRef();
  const authCtx = useContext(AuthContext);

  useEffect(() => {
    database.ref(`/profile/${authCtx.token}`).on("value", (value) => {
      if (authCtx.token) {
        setstate(value.val().name.split(" "));
      } else {
        return false;
      }
    });
  }, [authCtx.token]);

  const signInWithProvider = (provider) => {
    auth.onAuthStateChanged((authObj) => {
      auth.signInWithPopup(provider);
      if (!authObj) {
        return false;
      } else {
        // setname(authObj.displayName)
        authCtx.login(authObj.uid);
        database.ref(`/profile/${authObj.uid}`).set({
          name: authObj.displayName,
          createdAt: firebase.database.ServerValue.TIMESTAMP,
          email: authObj.email,
        });
      }
    });
  };

  const onGoogleSignUp = () => {
    signInWithProvider(new firebase.auth.GoogleAuthProvider());
    //  window.location.reload()
  };

  const LogOut = () => {
    authCtx.logout();
    window.location.reload();
  };

  const onLocalSignUp = (e) => {
    e.preventDefault();
    auth
      .createUserWithEmailAndPassword(
        emailInputRef.current.value,
        passwordInputRef.current.value
      )
      .then((authObj) => {
        console.log(authObj.user);
        database.ref(`/profile/${authObj.user.uid}`).set({
          name: nameInputRef.current.value,
          createdAt: firebase.database.ServerValue.TIMESTAMP,
          email: authObj.user.email,
        });
      })
      .catch((error) => {
        console.log(error.message);
      });

    e.target.reset();
  };

  return (
    <div className={classes.Form}>
      <Link to="/">Home</Link>
      {authCtx.token && <Profile LogOut={LogOut} />}
      {!authCtx.token && (
        <Form
          style={{ width: "60%" }}
          className={classes.Login}
          onSubmit={onLocalSignUp}
        >
          <h2>Login</h2>
          <Form.Group className="mb-3" htmlFor="name">
            <Form.Label>Name</Form.Label>
            <Form.Control type="text" placeholder="Name" ref={nameInputRef} />
            <br />
            <Form.Label>Username or email address </Form.Label>
            <Form.Control
              type="email"
              placeholder="Enter email"
              ref={emailInputRef}
            />
            <Form.Text>
              We'll never share your email with anyone else.
            </Form.Text>
          </Form.Group>

          <Form.Group className="mb-3" controlId="formBasicPassword">
            <Form.Label>Password</Form.Label>
            <Form.Control
              type="password"
              placeholder="Password"
              required
              ref={passwordInputRef}
              maxLength="25"
              minLength="6"
            />
          </Form.Group>
          <Form.Group className="mb-3" controlId="formBasicCheckbox">
            <Form.Check className="mb-3" type="checkbox" label="Remember me" />
          </Form.Group>
          <Button variant="primary" type="submit">
            Login
          </Button>
        </Form>
      )}
      {!authCtx.token && (
        <div className={classes.google}>
          <Button variant="primary" type="submit" style={{ width: "35%" }}>
            <BsFacebook style={{ fontSize: "24px" }} /> FaceBook (Not Working)
          </Button>
        </div>
      )}
      {!authCtx.token && (
        <div className={classes.google}>
          <Button
            variant="danger"
            type="submit"
            style={{ width: "35%" }}
            onClick={onGoogleSignUp}
          >
            <BsGoogle style={{ fontSize: "24px" }} /> Login With Google{" "}
          </Button>
        </div>
      )}
    </div>
  );
};

export default Login;
